// /modal
import CreateMaintainceOptionModal from '@/components/modal/maintainceOptionModal/createMaintainceOptionModal';
// 
import { BTN_TEXT, EQUIPMENT_DATA, PAGE_TITLE, STATUS_CODE, MSG, TITLE, EXPORT_NAME } from '@/utils/constantsFile';
// table
import VXETable from '@/components/common/Table';
// 
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
// API
import { maintainItemsGetList, addMaintainItem, updateMaintainItem, deleteMaintainItem } from '@/api/maintanceManagement';
import { exportMaintainBaseDataConfigTemplate } from '@/api/export';
import { exportImportMaintainBaseData } from '@/api/import';
// message service
import { showWarningMessage, showSuccessMessage } from '@/utils/message';
// 
import { showErrorNotify } from '@/utils/notify';
import ImportDialog from '@/components/common/ImportDialog';
export default {
  components: {
    CreateMaintainceOptionModal,
    VXETable,
    HeaderTitle,
    HeadOperationItem,
    ImportDialog
  },
  data() {
    return {
      EXPORT_NAME: EXPORT_NAME,
      TITLE: TITLE,
      MSG: MSG,
      PAGE_TITLE: PAGE_TITLE,
      BTN_TEXT: BTN_TEXT,
      EQUIPMENT_DATA: EQUIPMENT_DATA,
      isImportDialogShow: false,
      // 
      tableLoading: false,
      // /modal
      showCreateMaintain: false,
      // 
      isAddItem: true,
      // 
      selectOptions: null,
      // 
      maintainOptionData: {
        page: {
          total: 10,
          currentPage: 1,
          pageSize: 10,
          pageSizes: [5, 10, 20, 50, 100, 200, 500],
          perfect: true,
          slots: { left: 'pager_left' },
          layouts: ['Sizes', 'PrevJump', 'PrevPage', 'Number', 'NextPage', 'NextJump', 'FullJump', 'Total']
        },
        columns: [
          {
            type: 'radio',
            align: 'center',
            visible: true,
            width: '50'
          },
          // { type: 'checkbox', width: '50', align: 'center' },
          {
            field: 'maintainItemId',
            title: 'ID',
            align: 'center'
          },
          {
            field: 'maintainItemName',
            title: EQUIPMENT_DATA.MAINTANCE_OPTION,
            align: 'center'
          },
          {
            field: 'maintainItemRemark',
            title: '',
            align: 'center'
          }
          // {
          //   field: '',
          //   title: BTN_TEXT.OPERATION,
          //   width: '250',
          //   align: 'center',
          //   slots: {
          //     default: ({ row, column }) => {
          //       return [
          //         <el-button onClick={() => this.showCreateMaintainModal(row)} type="text">
          //           { BTN_TEXT.MODIFY }
          //         </el-button>,
          //         <el-button onClick={() => this.deleteMaintainOptionConfirm([row])} type="text">
          //           { BTN_TEXT.DELETE }
          //         </el-button>
          //       ];
          //     },
          //     header: ({ column }) => {
          //       return [
          //         <span>
          //           <span>{ column.title }</span>
          //           {/* <i class="el-icon-s-tools set-btn"></i> */}
          //         </span>
          //       ];
          //     }
          //   }
          // }
        ],
        data: []
      },
      // 
      selectRecords: [],
      input_byx: '',
      input_bz: ''
    };
  },
  methods: {
    // 
    buttonClickEvent(type) {
      // if (type === 'exit') {
      //   this.$emit('closePage');
      //   return;
      // }
      this[`${type}`]();
    },
    /**
     * @description:  modal
     * @return: void
    */
    showCreateMaintainModal() {
      this.showCreateMaintain = true;
    },
    /**
     * @description:  modal
     * @return: void
    */
    showUpdateMaintainModal() {
      var row = {};

      if (!this.$refs.xGrid.getRadioRecord()) {
        showWarningMessage('');
        return false;
      } else {
        row = this.$refs.xGrid.getRadioRecord();
      }
      if (row) {
        this.isAddItem = false;
        this.selectOptions = row;
      }
      this.showCreateMaintain = true;
    },
    /**
     * @description: 
     * @param {Object} info /modal
     * @return: void
    */
    addOrUpdateMaintainOption(info) {
      if (info.isAdd) {
        addMaintainItem(
          {
            maintainItemName: info.data,
            maintainItemRemark: info.data2
          }
        ).then(res => {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            this.showCreateMaintain = false;
            this.getMaintainOptionData();
            showSuccessMessage(res.message);
          } else {
            this.showCreateMaintain = false;
            showWarningMessage(res.message);
          }
        }).catch(err => {
          this.showCreateMaintain = false;
          showErrorNotify(err);
        });
      } else {
        updateMaintainItem({
          maintainItemId: info.id,
          maintainItemName: info.data,
          maintainItemRemark: info.data2
        }).then(res => {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            this.showCreateMaintain = false;
            this.getMaintainOptionData();
            showSuccessMessage(res.message);
          } else {
            this.showCreateMaintain = false;
            showWarningMessage(res.message);
          }
        }).catch(err => {
          this.showCreateMaintain = false;
          showErrorNotify(err);
        });
      }
    },
    /**
     * @description: /modal
     * @return: void
    */
    clickCreateMaintainCancel() {
      this.showCreateMaintain = false;
      this.isAddItem = true;
      this.selectOptions = null;
    },
    /**
     * @description: tablecheckbox
     * @param {Array} records 
     * @return: void
    */
    checkboxChangeEvent(records) {
      this.selectRecords = records;
    },
    /**
     * @description: 
     * @return: void
    */
    reset() {
      this.input_byx = '';
      this.input_bz = '';
      this.getMaintainOptionData();
    },
    getMaintainOptionData() {
      this.tableLoading = true;
      var params = {};
      if (this.maintainOptionData && this.maintainOptionData.page) {
        params = {
          current: this.maintainOptionData.page.currentPage,
          size: this.maintainOptionData.page.pageSize
        };
      }
      var condition = {
        conditionJson1: []
      };

      if (this.input_byx) {
        condition.conditionJson1.push({
          column: 'maintainItemName', // 
          operator: 1, // 
          ptype: 'string', // 
          type: 'like', // 
          value: this.input_byx // 
        });
      }
      if (this.input_bz) {
        condition.conditionJson1.push({
          column: 'maintainItemRemark', // 
          operator: 1, // 
          ptype: 'string', // 
          type: 'like', // 
          value: this.input_bz // 
        });
      }

      maintainItemsGetList(params, condition).then(res => {
        this.tableLoading = false;
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.maintainOptionData.data = res.rows;
          this.maintainOptionData.page.total = res.total;
        } else {
          showWarningMessage(res.message);
        }
      }).catch(err => {
        this.tableLoading = false;
        showErrorNotify(err);
      });
    },
    /**
     * @description: 
     * @param {int} currentPage 
     * @param {int} pageSize 
     * @return: void
    */
    handleMaintainOptionTablePageChange(currentPage, pageSize) {
      this.maintainOptionData.page.currentPage = currentPage;
      this.maintainOptionData.page.pageSize = pageSize;
      this.getMaintainOptionData();
    },
    toDelete() {
      var row = {};

      if (!this.$refs.xGrid.getRadioRecord()) {
        showWarningMessage('');
        return false;
      } else {
        row = this.$refs.xGrid.getRadioRecord();
      }
      this.deleteMaintainOptionConfirm([row]);
    },
    /**
     * @description: 
     * @param {Array} data 
     * @return: void
    */
    deleteMaintainOptionConfirm(data) {
      var deleteItems = [];
      data.forEach(element => {
        deleteItems.push(element.maintainItemId);
      });
      this.$confirm(MSG.CONFIRM_CHOOSE_DELETE, TITLE.PROMPT, {
        confirmButtonText: BTN_TEXT.CONFIRM,
        cancelButtonText: BTN_TEXT.CANCEL,
        type: 'warning'
      }).then(() => {
        this.deleteMaintainOption(deleteItems);
      }).catch(() => {
      });
    },
    /**
     * @description: 
     * @param {Array} deleteItems id 
     * @return: void
    */
    deleteMaintainOption(deleteItems) {
      deleteMaintainItem({
        ids: deleteItems
      }).then(res => {
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.getMaintainOptionData();
          showSuccessMessage(res.message);
        } else {
          this.showCreateMaintain = false;
          showWarningMessage(res.message);
        }
      }).catch(err => {
        this.showCreateMaintain = false;
        showErrorNotify(err);
      });
    },
    /**
     * @description:
     * @param {type}
     * @return:
     * @author: ZJM
     */
    importTable() {
      this.isImportDialogShow = true;
      setTimeout(() => {
        this.$refs.ImportDialog.isShow = true;
        this.$refs.ImportDialog.resetUploadProgressBar();
      }, 0);
    },
    closeImportDialog() {
      this.getMaintainOptionData();
      this.isImportDialogShow = false;
    },
    /**
     * @description:
     * @param {type}
     * @return:
     * @author: CH
     */
    async exportTemplate() {
      try {
        const res = await exportMaintainBaseDataConfigTemplate();
        this.$options.filters.setExportFunc(res, EXPORT_NAME.MAINTAIN_BASEDATACONFIG_TEMPLATE);
      } catch (error) {
        showErrorNotify(this.GLOBAL.TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description:
     * @param {type}
     * @return:
     * @author: Lois
     */
    submitImportFile(param) {
      const fileObj = param.file;
      const fd = new FormData();
      fd.append('file', fileObj);
      // 
      exportImportMaintainBaseData({}, fd, {
        onUploadProgress: event => {
          // eslint-disable-next-line prefer-const
          let uploadedPercentage = Math.floor(
            (event.loaded * 100) / event.total
          );
          // option.onProgress({ percent: uploadedPercentage });
          this.$refs.ImportDialog.uploadPercent = uploadedPercentage;
        }
      })
        .then(res => {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            param.onSuccess(res.message);
          } else if (res.rows) {
            console.log(res.rows);
            param.onError(res.message + '!' + res.rows);
          } else {
            param.onError(res.message);
          }
        });
    },
    refreshTable() {
      this.getMaintainOptionData();
    }
  },
  created() {
    this.getMaintainOptionData();
  }
};
