    /**
     * @description: 
     * @param checkedData //
     * @param tableDatas //table
     * @param contrastData //
     * @return: 
     * @author: ZJM
     */
    async saveRelatedDevice(checkedData, tableDatas, contrastData) {
      try {
        // 
        const selectedArr = [];
        // 
        const UnSelectedArr = [];
        // 
        if (checkedData.length === 0) {
          // devicecode
          for (var i in tableDatas) {
            UnSelectedArr.push(tableDatas[i].deviceCode);
          }
        } else {
          // 
          // 
          for (var p in tableDatas) {
            // 
            var unflag = 0;
            // 
            for (var j in checkedData) {
              if (tableDatas[p].deviceCode === checkedData[j].deviceCode) {
                unflag++;
              }
            }
            // 
            if (unflag === 0) {
              UnSelectedArr.push(tableDatas[p].deviceCode);
            }
          }
          // 
          for (var x in checkedData) {
            // 
            var flag = 0;
            // 
            if (contrastData.length > 0) {
              for (var y in contrastData) {
                if (checkedData[x].deviceCode === contrastData[y].deviceCode) {
                  flag++;
                }
              }
            }
            // 
            if (flag === 0) {
              selectedArr.push(checkedData[x].deviceCode);
            }
          }
        }
        const res = await deviceUserRelationshipInsertOrDeleteUserRelationshipDeviceAction(
          {
            userId: this.currentChangeRow.userId,
            deviceUserRelationshipEnum: 'MAINTAIN',
            relationshipDeviceCodeList: selectedArr,
            deleteDeviceCodeList: UnSelectedArr
          }
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          showSuccessMessage(res.message);
          this.$refs.relatedDevice.findTableList();
          this.$refs.relatedDevice.clearCheckboxRow();
          this.$refs.relatedDevice.deviceChecks = [];
          this.$refs.deviceDialog.findList();
        } else {
          // 
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
        this.tableLoading = false;
      }
    },
